/*
Navbar


*/
.navbar {

	display: flex;
    flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
	padding: 18px;
	background-color: #00308F;
	
}

.navbar-left-title {
	padding: 0;
	margin: 0;
	font-size: 20px;
	font-weight: 600;
	font-style: italic;
	font-family:Georgia, 'Times New Roman', Times, serif;
	color: orange;
	margin-right: 20px;
}


.categorybar-icon {
	font-size: 24px;
}



.navbar-right{
	display:flex;
	margin-left: 7px;
}

.navbar-right-mobile{
	display: none;
	margin-right: 35px;
}

.navbar-right-mobile i{
	cursor: pointer;
	color: orange;
}


@media only screen and (max-width: 820px)
{
	.navbar{
		justify-content: space-between;
		align-items: center;
	}
	.navbar-right{
		display: none;
	}

	.navbar-right-mobile{
		display: unset;
		align-items: center;
		display: inline-block;
	}
}